
#reservation-header-container p {

}
#reservation-selection-container {
    background-color: red;
    height: 30%;
    width: 100%;
}
#reservation-button-container {
    background-color: purple;
    color: white;
    height: 40px;
}
#reservation-fee-container {
    background-color: yellow;
    height: 120px;
}

#reservation-total-container {
    background-color: orange;
    padding: 20px;
}
#guests{
    display: flex;
    justify-content: space-between;
    height: 50px;
    text-align: left;
    padding: 10px;
    border: 1px solid black;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
  }
#guests button {
    height: 100%;
    width: 100%;
    background-color: transparent;
    border: none;
    text-align: right;
    cursor: pointer;
}



#guests-dropdown-content {
    display: block;
}
.guests-dropdown-item {
    display: flex;
    justify-content: space-around;
    height: 100px;
    width: 200px;
    padding-top: 10px;
}
.quantity-button-container {
    display: flex;
    height: 50px;
    width: 200px;

    justify-content: space-between;
}

.quantity-display {
    width: 50px;
    text-align: center;
}


#reservation-confirmation-overlay{

        position: fixed; 
        display: none;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.5); 
        z-index: 2;
        cursor: pointer; 
      
}

#reservation-confirmation {
    
    width: 40%;
    height: 50%;
    margin-left: 500px;
    margin-top: 100px;
    background-color: white;
    border-radius: 20px;
    padding: 100px;
 
    display: flex;
    justify-content: space-between;
}
#confirmation-price-details {
    width: 400px;
    font-size: 20px;
    margin-right: 50px;
    margin-top: 100px;
    padding: 10px;
}
#confirmation-title {
    font-size: 35px;
    margin-bottom: 100px;
    text-decoration: underline;
}

#confirmation-booking-title {
    font-size: 20px;
    font-weight: 600;
}


#date-confirmation-container {
    display: flex;
    justify-content: space-between;
}

.guests-confirmation-container {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
}

#confirmation-dates-title {
    font-size: 20px;
}

#left-arrow {
    width: 20px;
}

#date-confirmation-container p {
    font-size: 20px; 
}

.edit-button {

    text-decoration: underline;
}

input::-webkit-calendar-picker-indicator{
    display: none;
}

.confirmation-options-buttons{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }


  .confirmation-options-buttons button {
    height: 30px;
    width: 30px;
    margin: 10px;
    border-radius: 40px;
    background-color: transparent;
    border: 1px solid gray;
  }

  #max-message {
    color: red;
  }