#review-modal {
    z-index: 2000;
    position: fixed;
    inset: 0px;
    max-height: calc(var(--vh, 1vh) * 100);
    padding-top: calc(12px + env(safe-area-inset-top, 0));
    display: none;
    flex-direction: row;
    align-items: flex-end;
    justify-content: center;
  }
    #review-modal-background{
      position: fixed;
      display: none;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      background-color: rgba(236, 229, 229, 0.7);
    }
  
    #review-modal-content {
      display: none;
      flex-direction: column;
      height: fit-content;
      width: 600px;
      margin-left: 33vw;
      margin-top: 5%;
      position: absolute;
      background-color:white;
      padding: 40px;
      border-radius: 20px;
    }
  
    .toggle-review-button {
      cursor: pointer;
    }
  
    #review-modal-header {
      width: 100%;
      display: flex;
      text-align: center;
      justify-content: space-between;
      border-bottom: 1px solid rgb(187, 177, 177);
      padding: 10px;
    }
  
  #review-banner {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    width: 100%;
    height: fit-content;
    white-space: nowrap;
  }

  #review-banner > img {
    cursor: pointer;
  }
  
  .review-rating-item {
    display: flex;
    margin-top: 10%;
    width: 100%;
    justify-content: space-between;
  }

  .slide-container {
    width: 100%;
    height: 100%;
    padding: 10px;
    margin-left: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
  cursor: pointer;
  }
  
  .review-slider {
    grid-column-start: 2;
    width: 70%;
    cursor: pointer;
  
  }
  
  .review-track {
    top: 0;
    height: 11px;
    max-width: 100%;
    background: aqua;
  }
  
  .review-track.review-slider-track-0 {
    background: black;
  
  }
  
  
  
  .review-thumb {
    height: 15px;
    width: 15px;
    border-radius: 20px;
    background: black;
    position: relative;
  }

  #review-form-button-container {
    display: flex;
    margin-top: 10%;
  
  }

  #review-banner {
    margin-right: 40%;
  }
  
  #review-submit-button {
    cursor: pointer;
    display: inline-block;
    margin: 0px;
    position: relative;
    text-align: center;
    text-decoration: none;
    touch-action: manipulation;
  
  
    line-height: var(---s-l-myu);
  
    border-radius: 10px;
    outline: none;
    padding: 14px 24px;
    -webkit-tap-highlight-color: transparent;
    border: none;
    background: linear-gradient(to right, rgb(230, 30, 77) 0%, rgb(227, 28, 95) 50%, rgb(215, 4, 102) 100%);
    color: rgb(255, 255, 255);
    width: 100%;
  
  }

  #review-items-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .review-update-option {
    margin-right: 10px;
    font-weight: 600;
  }